.contactsList {
    display: flex;
    flex-direction: column;
    border-color: rgb(143, 100, 146);
    border-radius: 5px;
    justify-content: center;
    align-items: center;
    padding: 30px;
    margin-top: 40px;
}

.wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.contactsItem {
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin: 10px;
    width: 250px;
    background-color: rgb(143, 100, 146);
    color: #ffffff;
    height: 80px;
    border-radius: 5px;
    padding: 5px;

}

.contactBtn {
    margin-bottom: 10px;
    margin-top: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 20px;
    padding: 10px;
    border: none;
    border-radius: 5px;
    background-color: rgb(62, 22, 65);
    color: rgb(255, 255, 255);
    font-size: 12px;
    transition: all 500ms ease;
    cursor: pointer;
}

.contactBtn:hover {
    background-color: blueviolet;
    color: azure;
    box-shadow: 5px 5px 5px rgb(85, 84, 84);
}

.contactsName {
    font-weight: 600;
    margin-bottom: 5px;
    margin-top: 5px;
}

.contactImage {
    border-radius: 50px;
}